import React from "react";
import { Box, Typography, Link, useTheme, useMediaQuery, Collapse, Alert, TextField, Button } from '@mui/material';
import { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";

export default function RegisterScreen() {
    const [ username,     setUsername]     = useState("");
    const [ email,    setEmail]    = useState("");
    const [ password, setPassword] = useState("");
    const [ error,    setError]    = useState(false);

    const navigate    = useNavigate();
    const theme       = useTheme();
    const isNonMobile = useMediaQuery("(min-width: 1000px)");

    const config = {
        headers: {
            "Content-Type": "application/json",
        }
    }

    const registerHandler = async (e) => {
        e.preventDefault();

        try {
            await axios.post("/api/auth/register", { username, email, password }, config);
            navigate("/login");
        } catch (err) {
            console.log(err);
            if (err.response.data.error)  {
                setError(err.response.data.error);
            } else if (err.message) {
                setError(err.message);
            }
            setTimeout(() => setError(''), 5000);
        }
    }

    return (
        <Box
            width={isNonMobile ? "40%" : "80%"}
            p="2rem"
            m="2rem auto"
            borderRadius={5}
            backgroundColor={theme.palette.background.alt}
            sx={{ boxShadow: 5 }}
        >
            <Collapse in={error}>
                <Alert severity="error" sx={{ mb: 2 }}>
                    {error}
                </Alert>
            </Collapse>

            <form onSubmit={registerHandler}>
                <Typography variant="h3" textAlign="center">Sign Up</Typography>
                <TextField
                    label="User Name"
                    type="text"
                    fullWidth
                    required
                    value={username}
                    margin="normal"
                    onChange={(e) => setUsername(e.target.value)}  // Corrected here
                />
                <TextField
                    label="Email"
                    type="email"
                    fullWidth
                    required
                    value={email}
                    margin="normal"
                    onChange={(e) => setEmail(e.target.value)}  // Corrected here
                />
                <TextField
                    label="Password"
                    type="password"
                    fullWidth
                    required
                    value={password}
                    margin="normal"
                    onChange={(e) => setPassword(e.target.value)}  // Corrected here
                />
                <Button variant="contained" fullWidth type='submit' size="large" sx={{ color: "white", mt: 2 }}>
                    Sign Up
                </Button>
            </form>
            <Typography mt={2}>
                You already have an account? 
                <Link href="/login" ml={1}>Sign in</Link>
            </Typography>
        </Box>
    );
};
