import React from "react";
import { Box, Typography, Link, useTheme, useMediaQuery, Collapse, Alert, TextField, Button, Card, Stack } from '@mui/material';
import { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";

export default function ParagraphScreen() {
    const [ text,      setText ]    = useState("");
    const [ paragraph, setParagraph ] = useState("");
    const [ error,     setError ]   = useState(false);

    const theme       = useTheme();
    const isNonMobile = useMediaQuery("(min-width: 1000px)");

    const paragraphHandler = async (e) => {
        e.preventDefault();
        try {
            
            const { data } = await axios.post("/api/openai/paragraph", { text });
            setParagraph(data);
            console.log(data);
        } catch (err) {
            console.log(err);
            if (err.response.data.error)  {
                setError(err.response.data.error);
            } else if (err.message) {
                setError(err.message);
            }
            setTimeout(() => setError(''), 5000);
        }
    }

    return (
        <Box
            width={isNonMobile ? "50%" : "90%"}
            p="2rem"
            m="2rem auto"
            borderRadius={5}
            backgroundColor={theme.palette.background.alt}
            sx={{ boxShadow: 5 }}
        >
            <Collapse in={error}>
                <Alert severity="error" sx={{ mb: 2 }}>
                    {error}
                </Alert>
            </Collapse>

            <form onSubmit={paragraphHandler}>
                <Typography variant="h3" textAlign="center" sx={{ mb: "1.5rem" }}>Paragraph Generator</Typography>
                <Stack direction={"row"} spacing={1}>
                    <Box width="87%">
                        <TextField
                            placeholder="Enter paragraph topic here"
                            type="text"
                            multiline={true}
                            fullWidth
                            required
                            value={text}
                            onChange={(e) => setText(e.target.value)}  // Corrected here
                        />
                    </Box>
                    <Button disableElevation variant="contained"  type='submit' size="large" sx={{ color: "white" }}>
                            Generate
                    </Button>
                </Stack>
            </form>
            {paragraph ? 
                <Card mt={2} sx={{ textAlign: "center", mt:4, p:2, border:1, boxShadow:0, borderColor:"neutral.medium", borderRadius:2, height:"500px", bgcolor:"background.default"  }}>
                    <Typography>
                        {paragraph}
                    </Typography>
                </Card>
            :
                <Card mt={2} sx={{ textAlign: "center", mt:4, p:2, border:1, boxShadow:0, borderColor:"neutral.medium", borderRadius:2, height:"500px", bgcolor:"background.default"  }}>
                    <Typography variant="h3" color="neutral.main" sx={{ textAlign: "center", verticalAlign: "middle", lineHeight: "450px" }}>
                        Blurb will appear here
                    </Typography>
                </Card>
            }
            
            <Typography mt={2}>
                Not the tool are you were looking for?
                <Link href="/" ml={1}>Go back..</Link>
            </Typography>
        </Box>
    );
};
