import React from 'react';
import { Routes, Route } from 'react-router-dom';
import { useMemo }       from 'react';
import { ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { createTheme }   from '@mui/material/styles';
import { themeSettings } from './theme';
import HomeScreen        from './components/HomeScreen';
import LoginScreen       from './components/LoginScreen';
import RegisterScreen    from './components/RegisterScreen';
import SummaryScreen     from './components/SummaryScreen';
import ParagraphScreen   from './components/ParagraphScreen';
import ChatBotScreen     from './components/ChatBotScreen';
import JavaScriptScreen  from './components/JavaScriptScreen';
import ScifiImageScreen  from './components/ScifiImageScreen';
import Navbar            from './components/Navbar';

import { Elements }   from '@stripe/react-stripe-js';
import { loadStripe } from '@stripe/stripe-js';

const stripePromise = loadStripe(`${process.env.REACT_APP_STRIPE_PUBLISHABLE_KEY}`);

function App() {
  const theme = useMemo(() => createTheme(themeSettings()), []);
  return (
    <Elements stripe={stripePromise}>
      <div className="App">
        <ThemeProvider theme={theme}>
          <CssBaseline />
          <Navbar />
          <Routes>
            <Route exact path="/"         element={<HomeScreen />} />
            <Route exact path="/login"    element={<LoginScreen />} />
            <Route exact path="/register" element={<RegisterScreen />} />
            <Route exact path="/summary"  element={<SummaryScreen />} />
            <Route exact path="/paragraph"  element={<ParagraphScreen />} />
            <Route exact path="/chatbot"    element={<ChatBotScreen />} />
            <Route exact path="/js-convert" element={<JavaScriptScreen />} />
            <Route exact path="/scifi-img" element={<ScifiImageScreen />} />
          </Routes>
        </ThemeProvider>
      </div>
    </Elements>
  );
}

export default App;
