import React, { useState } from "react";
import axios from "axios";
import { Box, Typography, Link, useTheme } from '@mui/material';

export default function Navbar ()  {
    const theme = useTheme();
    const [ loggedIn, setLoggedIn ] = useState(JSON.parse(localStorage.getItem("authToken")));

    // const loggedIn = JSON.parse(localStorage.getItem("authToken"));
    const logoutHandler = async e => {
        e.preventDefault(); 
        try {
            await axios.post("/api/auth/logout").then(res => fullyLogout(res.data));
        } catch (err) {
            console.log(err);
        }
    }

    const fullyLogout = (data) => {
        if  (data.success) {
            localStorage.removeItem("authToken");
            window.location.reload();
        }
    }

    const checkRefresh = async () => {
        try {
            const token = await axios.get("/api/auth/refresh-token");
            if (!token.data) {
                localStorage.removeItem("authToken");
                setLoggedIn(false);
                logoutHandler();
            }
        } catch (err) {
            console.log(err);
        }
    }

    checkRefresh();

    return (
        <Box width="100%" backgroundColor={theme.palette.background.alt} textAlign="center" p="1rem 6%" sx={{boxShadow:3, mb: 3}}>
            <Typography variant="h1" color="primary" fontWeight="bold"><Link underline="none" href='/' >SaaSAI</Link></Typography>
            {loggedIn 
                ? <Link href="/" onClick={logoutHandler} p={1}>Logout</Link>
                : <>
                    <Link href="/login" p={1}>Login</Link>
                    <Link href="/register" p={1}>Register</Link>
                </>
            }
        </Box>
    );
};